
.nav-link.Ripple-parent {
  padding-top: 13px;
}

.lds-css {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
@keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-webkit-keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}
@-webkit-keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}
.lds-double-ring {
  position: relative;
}
.lds-double-ring div {
  position: absolute;
  width: 160px;
  height: 160px;
  top: 20px;
  left: 20px;
  border-radius: 50%;
  border: 8px solid #000;
  border-color: #93dbe9 transparent #93dbe9 transparent;
  -webkit-animation: lds-double-ring 1.4s linear infinite;
  animation: lds-double-ring 1.4s linear infinite;
}
.lds-double-ring div:nth-child(2) {
  width: 140px;
  height: 140px;
  top: 30px;
  left: 30px;
  border-color: transparent #689cc5 transparent #689cc5;
  -webkit-animation: lds-double-ring_reverse 1.4s linear infinite;
  animation: lds-double-ring_reverse 1.4s linear infinite;
}
.lds-double-ring {
  width: 72px !important;
  height: 72px !important;
  -webkit-transform: translate(-36px, -36px) scale(0.36) translate(36px, 36px);
  transform: translate(-36px, -36px) scale(0.36) translate(36px, 36px);
}

.col-sm-12.col-md-6 {
  width: 160px;
}

.form-control.form-control-sm {
